import { ChangeDetectorRef, Component, Injector, Input, ViewChild } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { EditableFormComponent } from 'src/app/components/editable-form/editable-form.component';
import { GridComponent } from 'src/app/components/grid/grid.component';
import { ToolbarButton } from 'src/app/components/toolbar/toolbar.component';
import { PageFrameBase } from 'src/app/modules/base/page-frame-base';
import { PlanoTrabalho } from 'src/app/models/plano-trabalho.model';
import { PlanoTrabalhoEntrega } from 'src/app/models/plano-trabalho-entrega.model';
import { PlanoTrabalhoEntregaDaoService } from 'src/app/dao/plano-trabalho-entrega-dao.service';
import { PlanoTrabalhoDaoService } from 'src/app/dao/plano-trabalho-dao.service';
import { IIndexable } from 'src/app/models/base.model';
import { LookupItem } from 'src/app/services/lookup.service';
import { EntregaDaoService } from 'src/app/dao/entrega-dao.service';
import { InputSelectComponent } from 'src/app/components/input/input-select/input-select.component';
import { PlanoEntregaDaoService } from 'src/app/dao/plano-entrega-dao.service';
import { PlanoEntregaEntregaDaoService } from 'src/app/dao/plano-entrega-entrega-dao.service';
import { PlanoEntrega } from 'src/app/models/plano-entrega.model';
import { PlanoTrabalhoService } from '../plano-trabalho.service';

@Component({
  selector: 'plano-trabalho-list-entrega',
  templateUrl: './plano-trabalho-list-entrega.component.html',
  styleUrls: ['./plano-trabalho-list-entrega.component.scss']
})
export class PlanoTrabalhoListEntregaComponent extends PageFrameBase {
  @ViewChild(EditableFormComponent, { static: false }) public editableForm?: EditableFormComponent;
  @ViewChild(GridComponent, { static: false }) public grid?: GridComponent;
  @ViewChild('origem', { static: false }) public origem?: InputSelectComponent;
  @ViewChild('entregaMesmaUnidade', { static: false }) public entregaMesmaUnidade?: InputSelectComponent;
  @ViewChild('entregaOutraUnidade', { static: false }) public entregaOutraUnidade?: InputSelectComponent;
  @ViewChild('entregaCatalogo', { static: false }) public entregaCatalogo?: InputSelectComponent;
  @Input() set control(value: AbstractControl | undefined) { super.control = value; } get control(): AbstractControl | undefined { return super.control; }
  @Input() set entity(value: PlanoTrabalho | undefined) { super.entity = value; } get entity(): PlanoTrabalho | undefined { return super.entity; }
  @Input() set disabled(value: boolean) { if (this._disabled != value) this._disabled = value; } get disabled(): boolean { return this._disabled; }
  @Input() set noPersist(value: string | undefined) { super.noPersist = value; } get noPersist(): string | undefined { return super.noPersist; }
  @Input() cdRef: ChangeDetectorRef;
  @Input() set entregasDoPlanoEntrega(value: LookupItem[]) {
    if (JSON.stringify(value) != JSON.stringify(this._entregasDoPlanoEntrega)) {
      this._entregasDoPlanoEntrega = value;
      this.entregasMesmaUnidade = this.carregarEntregasMesmaUnidade();
      this.cdRef.detectChanges();
    }
  }
  get entregasDoPlanoEntrega(): LookupItem[] { return this._entregasDoPlanoEntrega; }

  public get items(): PlanoTrabalhoEntrega[] {
    if (!this.gridControl.value) this.gridControl.setValue(new PlanoTrabalho());
    if (!this.gridControl.value.entregas) this.gridControl.value.entregas = [];
    return this.gridControl.value.entregas;
  }
  public options: ToolbarButton[] = [];
  public entregaDao?: EntregaDaoService;
  public planoTrabalhoDao?: PlanoTrabalhoDaoService;
  public planoEntregaDao?: PlanoEntregaDaoService;
  public peeDao?: PlanoEntregaEntregaDaoService;
  public planoTrabalhoService: PlanoTrabalhoService;
  public totalForcaTrabalho: number = 0;
  public entregasMesmaUnidade: LookupItem[] = [];
  public entregasOutraUnidade: LookupItem[] = [];
  public entregasCatalogo: LookupItem[] = [];
  public planoEntregaOutraUnidade?: PlanoEntrega | null;

  private _disabled: boolean = false;
  private _entregasDoPlanoEntrega: LookupItem[] = [];

  constructor(public injector: Injector) {
    super(injector);
    this.dao = injector.get<PlanoTrabalhoEntregaDaoService>(PlanoTrabalhoEntregaDaoService);
    this.cdRef = injector.get<ChangeDetectorRef>(ChangeDetectorRef);
    this.entregaDao = injector.get<EntregaDaoService>(EntregaDaoService);
    this.planoTrabalhoDao = injector.get<PlanoTrabalhoDaoService>(PlanoTrabalhoDaoService);
    this.planoEntregaDao = injector.get<PlanoEntregaDaoService>(PlanoEntregaDaoService);
    this.planoTrabalhoService = injector.get<PlanoTrabalhoService>(PlanoTrabalhoService);
    this.peeDao = injector.get<PlanoEntregaEntregaDaoService>(PlanoEntregaEntregaDaoService);
    this.join = ["entrega", "plano_entrega_entrega.entrega"];
    this.form = this.fh.FormBuilder({
      origem: { default: null },
      entregaMesmaUnidade: { default: null },
      entregaOutraUnidade: { default: null },
      entregaCatalogo: { default: null },
      descricao: { default: "" },
      forca_trabalho: { default: 1 },
      plano_trabalho_id: { default: null },
      entrega_id: { default: null },
      plano_entrega_entrega_id: { default: null }
    }, this.cdRef, this.validate);
  }

  /**
   * Mtodo chamado para a validao dos campos do formulrio, por ocasio da edio ou insero de itens.
   * @param control 
   * @param controlName 
   * @returns 
   */
  public validate = (control: AbstractControl, controlName: string) => {
    let result = null;
    if (['descricao', 'forca_trabalho'].indexOf(controlName) >= 0 && !control.value?.length) result = "Obrigatrio!";
    if (['forca_trabalho'].indexOf(controlName) >= 0 && (control.value < 1 || control.value > 100)) result = "Deve estar entre 1 e 100";
    if (['entrega_id'].indexOf(controlName) >= 0) {
      if (this.form?.controls.origem.value == 'CATALOGO' && !control.value) result = "Este campo no pode ser nulo!";
      let cont = this.entity?.entregas?.filter(e => !!e.entrega_id && !e.plano_entrega_entrega_id && e.id != this.grid?.editing?.id).map(e => e.entrega_id).reduce((acc, id) => { if (id === control.value) return acc + 1; else return acc; }, 0) || 0; // (*1)
      if (cont > 0) result = "Esta entrega est em duplicidade!";
    }
    if (['plano_entrega_entrega_id'].indexOf(controlName) >= 0) {
      if (['MESMA_UNIDADE', 'OUTRA_UNIDADE'].includes(this.form?.controls.origem.value) && !control.value) result = "Este campo no pode ser nulo!";
      let cont = this.entity?.entregas?.filter(e => !e.entrega_id && !!e.plano_entrega_entrega_id && e.id != this.grid?.editing?.id).map(e => e.plano_entrega_entrega_id).reduce((acc, id) => { if (id === control.value) return acc + 1; else return acc; }, 0) || 0; // (*2)
      if (cont > 0) result = "Esta entrega est em duplicidade!";
    }
    return result;
  }

  /**
   * Mtodo chamado na inicializao do componente. Neste momento so carregadas as entregas do catlogo e as entregas da mesma unidade do plano de trabalho,
   * visto que esses itens no se alteram durante a vida do componente e podero ser utilizados durante sua utilizao.
   */
  async ngOnInit() {
    super.ngOnInit();
    this.entity = this.metadata?.entity || this.entity;
    this.totalForcaTrabalho = Math.round(this.somaForcaTrabalho(this.entity?.entregas) * 100) / 100;
    this.entregasCatalogo = await this.carregarEntregasCatalogo();
    this.entregasMesmaUnidade = this.carregarEntregasMesmaUnidade();
    this.entity!._metadata = this.entity!._metadata || {};
    this.entity!._metadata.idPlanoEntregas = this.entregasMesmaUnidade[0]?.data.plano_entrega_id;
    this.entity!._metadata.novaEntrega = undefined;
  }

  /**
   * Mtodo chamado para inserir uma entrega de plano de trabalho no grid, seja este componente persistente ou no.
   * @returns 
   */
  public async addEntrega() {
    return Object.assign(new PlanoTrabalhoEntrega(), {
      _status: this.isNoPersist ? "ADD" : "",
      id: this.dao!.generateUuid(),
      plano_trabalho_id: this.entity?.id
    }) as IIndexable;
  }

  /**
   * Mtodo utilizado durante a incluso/alterao de uma entrega de plano de trabalho no grid, seja este componente persistente ou no
   * @param form 
   * @param row 
   */
  public async loadEntrega(form: FormGroup, row: any) {
    form.controls.descricao.setValue(row.descricao);
    form.controls.forca_trabalho.setValue(row.forca_trabalho);
    form.controls.plano_trabalho_id.setValue(row.plano_trabalho_id);
    if (!row.entrega_id?.length && !row.plano_entrega_entrega_id?.length) { //  uma nova entrega
      form.controls.origem.setValue('MESMA_UNIDADE');
      form.controls.entrega_id.setValue(null);
      form.controls.plano_entrega_entrega_id.setValue(null);
    } else if (!row.plano_entrega_entrega_id?.length && !!row.entrega_id?.length) { //  uma entrega do tipo catlogo
      form.controls.origem.setValue('CATALOGO');
      form.controls.entrega_id.setValue(row.entrega_id);
      form.controls.plano_entrega_entrega_id.setValue(null);
    } else if (!row.entrega_id?.length && !!row.plano_entrega_entrega_id?.length && (row._metadata.entrega_selecionada?.plano_entrega_id || row.plano_entrega_entrega?.plano_entrega_id) == this.entity?.plano_entrega_id) {
      form.controls.origem.setValue('MESMA_UNIDADE');
      form.controls.entrega_id.setValue(null);
      form.controls.plano_entrega_entrega_id.setValue(row.plano_entrega_entrega_id);
    } else if (!row.entrega_id?.length && !!row.plano_entrega_entrega_id?.length && (row.objeto?.plano_entrega_id || row.plano_entrega_entrega?.plano_entrega_id) != this.entity?.plano_entrega_id) {
      form.controls.origem.setValue('OUTRA_UNIDADE');
      form.controls.entrega_id.setValue(null);
      await this.carregarEntregasOutraUnidade(row.plano_entrega_entrega?.plano_entrega_id);
      form.controls.plano_entrega_entrega_id.setValue(row.plano_entrega_entrega_id);
    }
  }

  /**
   * Mtodo chamado para a excluso de uma entrega de plano de trabalho do grid, seja este componente persistente ou no. 
   * @param row 
   * @returns 
   */
  public async removeEntrega(row: any) {
    let confirm = await this.dialog.confirm("Exclui ?", "Deseja realmente excluir?");
    if (confirm) {
      this.loading = true;
      try {
        this.isNoPersist ? Object.assign(row, { _status: "DELETE" }) : await this.dao?.delete(row.id);
      } finally {
        this.loading = false;
      }
      this.totalForcaTrabalho = Math.round((this.totalForcaTrabalho - ((row as PlanoTrabalhoEntrega).forca_trabalho * 1)) * 100) / 100;
      return this.isNoPersist ? false : true; // (*3)
    } else {
      return false;
    }
  }

  /**
   * Mtodo chamado no salvamento de uma entrega do plano de trabalho do grid, seja este componente persistente ou no.
   * @param form 
   * @param row 
   * @returns 
   */
  public async saveEntrega(form: FormGroup, row: any) {
    this.entity!._metadata = this.entity!._metadata || {};
    this.entity!._metadata.novaEntrega = row as PlanoTrabalhoEntrega;
    this.entity!._metadata.novaEntrega.entrega_id = this.form?.controls.entrega_id.value ?? null;
    this.entity!._metadata.novaEntrega.plano_entrega_entrega_id = this.form?.controls.plano_entrega_entrega_id.value;
    this.entity!._metadata.novaEntrega.descricao = this.form?.controls.descricao.value;
    this.entity!._metadata.novaEntrega.forca_trabalho = this.form?.controls.forca_trabalho.value;
    this.loading = true;
    try {
      if (!this.isNoPersist) {
        this.entity!._metadata.novaEntrega = await (this.dao as PlanoTrabalhoEntregaDaoService).save(this.entity!._metadata.novaEntrega, this.join);
        if (this.grid?.adding) this.grid!.items[this.grid!.items.length - 1].id = '';  // (*4)
      }
    } catch (e: any) {
      this.error(e.message ? e.message : e.toString() || e);
    } finally {
      this.entity!._metadata.novaEntrega.entrega = this.entregaCatalogo?.selectedItem?.data || null;
      this.entity!._metadata.novaEntrega.plano_entrega_entrega = this.entregaMesmaUnidade?.selectedItem?.data || this.entregaOutraUnidade?.selectedItem?.data || null;
      row.forca_trabalho = this.form?.controls.forca_trabalho.value * 1;
      this.totalForcaTrabalho = Math.round(this.somaForcaTrabalho(this.entity?.entregas) * 100) / 100;
      this.loading = false;
    }
    return this.entity!._metadata.novaEntrega;
  }

  /**
   * Mtodo chamado para somar os percentuais das foras de trabalho do array de entregas passado como parmetro.
   * @param entregas Array de entregas do plano de trabalho
   * @returns 
   */
  public somaForcaTrabalho(entregas: PlanoTrabalhoEntrega[] = []): number {
    return entregas.map(x => x.forca_trabalho * 1).reduce((a, b) => a + b, 0);
  }

  /**
   * Mtodo chamado na inicializao do componente para armazenar as entregas da mesma unidade do plano de trabalho.
   * @returns 
   */
  public carregarEntregasMesmaUnidade(): LookupItem[] {
    if (!this.entity?.id?.length) return this.entregasDoPlanoEntrega; // (*5)
    let entregasPlanoEntrega = this.entity?.plano_entrega?.entregas || [];
    //let result = entregasPlanoEntrega.map(epe => Object.assign({}, { key: epe.id, value: epe.entrega?.nome || epe.descricao, data: epe }));
    return entregasPlanoEntrega.map(epe => Object.assign({}, { key: epe.id, value: epe.entrega?.nome || epe.descricao, data: epe }));
  }

  /**
   * Mtodo chamado para carregar as entregas de uma outra unidade, com base no seu plano de entregas passado como parmetro.
   * @param idPlanoOuPlano ID do plano de entregas ou o seu objeto completo.
   */
  public async carregarEntregasOutraUnidade(idPlanoOuPlano: string | PlanoEntrega): Promise<void> {
    this.cdRef.detectChanges();
    this.planoEntregaOutraUnidade = typeof idPlanoOuPlano == 'string' ? await this.planoEntregaDao!.getById(idPlanoOuPlano, ["entregas.entrega", "unidade"]) : idPlanoOuPlano;
    this.entregasOutraUnidade = this.planoEntregaOutraUnidade?.entregas.map(epe => Object.assign({}, { key: epe.id, value: epe.entrega?.nome || epe.descricao, data: epe })) || [];
  }

  /**
   * Mtodo chamado para carregar as entregas existentes no Catlogo de Entregas.
   * @returns 
   */
  public async carregarEntregasCatalogo(): Promise<LookupItem[]> {
    let result: LookupItem[] = [];
    result = (await this.entregaDao?.query().getAll())?.map(ee => Object.assign({}, { key: ee.id, value: ee.nome, data: ee })) || [];
    return result;
  }

  /* ---------  TRATAMENTO DOS EVENTOS ----------- */

  public onOrigemChange(row: any) {
    let value = this.form!.controls.origem.value;
    if (['MESMA_UNIDADE', 'OUTRA_UNIDADE'].includes(value)) {
      this.form?.controls.entrega_id.setValue(null);
      this.cdRef.detectChanges();
      if (value == "OUTRA_UNIDADE") this.entregaOutraUnidade?.onSearchClick(this.entregaOutraUnidade?.searchRoute);
    } else if (value == 'CATALOGO') {
      this.form?.controls.plano_entrega_entrega_id.setValue(null);
    }
  }

  public onEntregaMesmaUnidadeChange(event: Event) {
    if (this.entregaMesmaUnidade?.selectedItem) {
      this.form?.controls.descricao.setValue(this.entregaMesmaUnidade?.selectedItem?.value || '');
      this.form?.controls.plano_entrega_entrega_id.setValue(this.entregaMesmaUnidade?.selectedItem?.key);
      this.cdRef.detectChanges();
    }
  }

  public onEntregaOutraUnidadeChange(event: Event) {
    if (this.entregaOutraUnidade?.selectedItem) {
      this.form?.controls.descricao.setValue(this.entregaOutraUnidade?.selectedItem?.value || '');
      this.form?.controls.plano_entrega_entrega_id.setValue(this.entregaOutraUnidade?.selectedItem?.key);
      this.cdRef.detectChanges();
    }
  }

  public onEntregaCatalogoChange(event: Event) {
    if (this.entregaCatalogo?.selectedItem) {
      this.form?.controls.descricao.setValue(this.entregaCatalogo?.selectedItem?.value || '');
      this.form?.controls.entrega_id.setValue(this.entregaCatalogo?.selectedItem?.key);
      this.cdRef.detectChanges();
    }
  }

  public onForcaTrabalhoChange(row: any) {
    let index = this.items.findIndex(x => x["id"] == row["id"]);
    this.totalForcaTrabalho = Math.round((this.somaForcaTrabalho(this.grid?.items as PlanoTrabalhoEntrega[]) - (this.items[index].forca_trabalho * 1) + (this.form?.controls.forca_trabalho.value * 1)) * 100) / 100;
  }
}


/*
TESTES

                                                  GRID PERSISTENTE                      GRID NO-PERSISTENTE
                                              Fluxo Feliz    Validaes                Fluxo Feliz    Validaes
Incluso Entrega Catlogo                          
Incluso Entrega Mesma Unidade                     
Incluso Entrega Outra Unidade                     
Alterao Entrega Catlogo                         
Alterao Entrega Mesma Unidade                    
Alterao Entrega Outra Unidade                    
Cancelamento Entrega Catlogo                      
Cancelamento Entrega Mesma Unidade                 
Cancelamento Entrega Outra Unidade                 
Excluso Entrega Catlogo                          
Excluso Entrega Mesma Unidade                     
Excluso Entrega Outra Unidade                     

*/

/*
OBSERVAES:

(*1)  let cont = this.entity?.entregas?.filter(e => !!e.entrega_id && !e.plano_entrega_entrega_id && e.id != this.grid?.editing?.id).map(e => e.entrega_id).reduce((acc, id) => { if(id === control.value) return acc + 1; else return acc; }, 0) || 0;
Filtra as entregas da mesma unidade do plano de trabalho excluindo a entrega que est sendo editada. Em seguida conta aquelas que possuem o mesmo ID da que est sendo editada e dessa forma o resultado deve ser igual a 0, caso contrrio a entrega editada
j existe no grid.

(*2)  let cont = this.entity?.entregas?.filter(e => !e.entrega_id && !!e.plano_entrega_entrega_id && e.id != this.grid?.editing?.id).map(e => e.plano_entrega_entrega_id).reduce((acc, id) => { if(id === control.value) return acc + 1; else return acc; }, 0) || 0;
Idem a obs 1, em relao s entregas de outras unidades.

(*3)  return this.isNoPersist ? false : true;
Aps confirmada a remoo pelo usurio, a entrega  setada com _status = "DELETE" e  excluda da tela e sua confirmao  retornada para o mtodo onDeleteItem() do gridComponent, que a excluir tambm dos items do grid.
Entretanto, quando se trata de um grid no persistente, ela no deve ser excluda dos items do grid, pois sua excluso s se efetivar quando entity for salva, levando
todas as suas entregas para o back-end, que finalmente excluir as que possurem _status = "DELETE".

(*4)  if(this.grid?.adding) this.grid!.items[this.grid!.items.length-1].id = '';
No caso de o componente ser 'persistente', essa linha apaga o ID que foi criado pelo banco de dados permitindo ao mtodo saveItem do grid.component localiz-lo na sua lista de items para atualizar suas informaes na tela.

(*5)  if(!this.entity?.id?.length) return this.entregasPlanoEntrega;
Quando o componente for chamado por um plano de trabalho (entity) que j existe, no poder haver alterao no seu plano de entregas, portanto as entregas da mesma unidade do plano de trabalho sero lidas
na prpria entity. No entanto, quando o plano de trabalho (entity) ainda no existir (!this.entity?.id?.length), essas entregas vo variar de acordo com o plano de entregas
selecionado no inputSelectComponent, e portanto elas sero lidas na varivel que reflete essa escolha (this.entregasPlanoEntrega), e essa reflexo ocorre por causa do bind @Input() set/get entregasPlanoEntrega.


----------------------+--------------------------------------+----------------------------+--------+------------------------------------
ID do Plano           |       Pode ser que a 'row'           |  Caractersticas           | inputs | entrega_id 
de Trabalho           |                                      |                            | search | plano_entrega_entrega_id
----------------------+--------------------------------------+----------------------------+--------+------------------------------------
                      | - J tenha vindo do banco (TIPO I)   |                            | nenhum | um dos dois
                      |   (foi carregada junto com o Plano)  |                            |        |
                      |--------------------------------------+----------------------------+--------+------------------------------------
  EXISTE              | - Tenho sido salva agora no banco    |                            | um dos | um dos dois
  - Ento o Grid     |   (TIPO II)                          |                            | trs   |
    Persistente       |--------------------------------------+----------------------------+--------+------------------------------------
                      | - Esteja sendo criada agora          | addEntrega loadEntrega     | um dos | nenhum
                      |                                      |                            | trs   |
                      |--------------------------------------+----------------------------+--------+------------------------------------     
                      | - Seja do TIPO I e esteja sendo      | loadEntrega                | um dos | um dos dois
                      |   editada agora                      |                            | trs   |
                      +--------------------------------------+----------------------------+--------+------------------------------------
                      | - Seja do TIPO II e esteja sendo     | loadEntrega                | um dos | um dos dois
                      |   editada agora                      |                            | trs   |
----------------------+--------------------------------------+----------------------------+--------+------------------------------------
  NO                 | - J exista nos itens do grid        |                            | nenhum | um dos dois
  EXISTE              |   (foi salva agora, no no banco)    | Possui _status == "ADD"    |        |
  - Ento o Grid     |--------------------------------------+----------------------------+--------+------------------------------------
    No-persistente   | - Esteja sendo criada agora          | Possui _status == "ADD"    | um dos | nenhum
                      |                                      | addEntrega loadEntrega     | trs   |
                      |--------------------------------------+----------------------------+--------+------------------------------------
                      | - Tenha acabado de ser criada e      | Possui _status == "ADD"    | um dos | um dos dois
                      |   j est sendo editada              | loadEntrega                | trs   |
------------------------------------------------------------------------------------------+--------+------------------------------------
(*) O ADD  inserido pelo mtodo addEntrega e s sai quando persiste no banco e volta

*/